import React from 'react'
import styled from 'styled-components'

const projectGitHubURL = 'https://github.com/alessioforte/transee'

export const Header = () => (
  <Head>
    <Box>
      <Github />
    </Box>
  </Head>
)

const Head = styled.div`
  height: 60px;
  width: 100%;
  background: #1a1a1a;
  display: flex;
  align-items: center;
  justify-content: center;
`
const Box = styled.div`
  width: 680px;
  display: flex;
  flex-direction: row-reverse;

  @media screen and (max-width: 800px) {
    padding: 0 30px;
  }
`

export const Github = () => (
  <Svg
    id="transee-github"
    onClick={function() {
      window.open(projectGitHubURL)
    }}
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 32.58 31.77"
    width="40px"
    height="40px"
  >
    <path
      d="M31.19,16.84A16.29,16.29,0,0,0,26,48.59a0.82,0.82,0,0,0,1.11-.79c0-.39,0-1.41,0-2.77-4.53,1-5.49-2.18-5.49-2.18a4.31,4.31,0,0,0-1.81-2.38c-1.48-1,.11-1,0.11-1a3.42,3.42,0,0,1,2.5,1.68,3.47,3.47,0,0,0,4.74,1.35,3.48,3.48,0,0,1,1-2.18c-3.62-.41-7.42-1.81-7.42-8.05a6.3,6.3,0,0,1,1.68-4.37,5.86,5.86,0,0,1,.16-4.31s1.37-.44,4.48,1.67a15.44,15.44,0,0,1,8.16,0c3.11-2.11,4.48-1.67,4.48-1.67a5.85,5.85,0,0,1,.16,4.31,6.29,6.29,0,0,1,1.67,4.37c0,6.26-3.81,7.63-7.44,8a3.89,3.89,0,0,1,1.11,3c0,2.18,0,3.93,0,4.47a0.82,0.82,0,0,0,1.12.78A16.29,16.29,0,0,0,31.19,16.84Z"
      transform="translate(-14.91 -16.84)"
    />
  </Svg>
)

const Svg = styled.svg`
  width: 35px;
  height: 35px;
  & path {
    fill: #fff;
    fill-rule: evenodd;
  }
  &:hover path {
    fill: #03a9f4;
  }
  &:active {
    opacity: 0.5;
  }
`



