import React from 'react'
import styled from 'styled-components'

import { Linkedin } from './linkedin'
import { Github } from './github'

const profileGitHubURL = 'https://github.com/alessioforte'
const profileLinkedinURL = 'https://www.linkedin.com/in/alessio-forte/'

export const Footer = () => (
  <Box>
    <Content>
      <div>Made with <span style={{ color: 'darkRed' }}>❤</span> by Alessio Forte</div>
      <div>Copyright © 2018</div>
      <Icons>
        <Link
          onClick={function() {
            window.open(profileGitHubURL)
          }}
        >
          <Github />
        </Link>
        <Link
          onClick={function() {
            window.open(profileLinkedinURL)
          }}
        >
          <Linkedin />
        </Link>
      </Icons>
    </Content>
  </Box>
)

const Content = styled.div`
  margin-top: 24px;
  font-size: 12px;
  color: #999;
  letter-spacing: 0.1rem;
  text-align: center;
  line-height: 1.1rem;
`
const Icons = styled.div`
  margin-top: 16px;
`
const Link = styled.a`
  margin: 8px;
  height: 20px;
  width: 20px;
`
const Box = styled.div`
  padding: 9px 0;
  width: 100%;
  height: 256px;
  display: flex;
  justify-content: center;
  background: #1a1a1a;
`



