import React, { Component } from 'react'
import styled from 'styled-components'
import sp100free from '../../assets/sp100free.png'

const dmgURL =
  'https://github.com/alessioforte/transee/releases/download/v1.2.4/Transee-1.2.5.dmg'
const exeURL =
  'https://github.com/alessioforte/transee/releases/download/v1.2.4/transee-setup-1.2.5.exe'
const softpediaURL =
  'http://www.softpedia.com/get/Office-tools/Other-Office-Tools/Transee.shtml'


export class Downloads extends Component {
  render() {
    return (
      <Download id='downloads'>
        <Title>
          <h1>Download</h1>
        </Title>

        <Container>
          <Box>
            <Apple className="macOS" />
          </Box>

          <Box>
            <Windows className="win32" />
            <Vers>
              <li>Windows 7 64 bit</li>
              <li>Windows 8 64 bit</li>
              <li>Windows 10 64 bit</li>
            </Vers>
          </Box>
        </Container>

        <Softpedia>
          <Img id='softpedia' src={sp100free} alt='softpedia' onClick={() => {window.open(softpediaURL)}}/>
          Softpedia guarantees that Transee is 100% Free, which means it does not contain any form of malware, including but not limited to: spyware, viruses, trojans and backdoors.
        </Softpedia>
      </Download>
    )
  }
}
const Title = styled.div`
  color: #fff;
  background: #03a9f4;
  width: 100%;
  text-align:center;
  padding: 32px 0;
`
const Download = styled.div`
  box-sizing: border-box;
  display: flex;
  flex-direction:column;
  align-items:center;
  width: 100%;
`
const Container = styled.div`
  margin-top: 50px;
  display: flex;
  max-width: 580px;
  justify-content:flex-start;

  @media screen and (max-width: 800px) {
    flex-direction:column;
    align-items:center;
  }
`
const Box = styled.div`
  margin: 32px;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: flex-start;
  max-width: 600px;
`
const Vers = styled.div`
  margin: 24px;
  font-size: 14px;
  color: #2a2a2a;
`
const Softpedia = styled.div`
  display: flex;
  align-items:center;
  max-width: 580px;
  padding: 24px;
  margin-bottom: 32px;

  @media screen and (max-width: 800px) {
    flex-direction:column;
    text-align: center;
    margin: 0 0 50px 0;
  }
  @media screen and (max-width: 300px) {
    margin: 0;
    padding: 0;
  }
`
const Img = styled.img`
  margin: 32px;
  cursor: pointer;
  object-fit: contain;

  @media screen and (max-width: 200px) {
    width: 100%;
  }
`

const Apple = props => {
  return (
    <Button
      className={props.className}
      onClick={() => {
        window.location.assign(dmgURL)
      }}
    >
      macOS
      <Svg
        className={props.className}
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 170 170"
        width="20px"
        height="20px"
      >
        <path d="m150.37 130.25c-2.45 5.66-5.35 10.87-8.71 15.66-4.58 6.53-8.33 11.05-11.22 13.56-4.48 4.12-9.28 6.23-14.42 6.35-3.69 0-8.14-1.05-13.32-3.18-5.197-2.12-9.973-3.17-14.34-3.17-4.58 0-9.492 1.05-14.746 3.17-5.262 2.13-9.501 3.24-12.742 3.35-4.929 0.21-9.842-1.96-14.746-6.52-3.13-2.73-7.045-7.41-11.735-14.04-5.032-7.08-9.169-15.29-12.41-24.65-3.471-10.11-5.211-19.9-5.211-29.378 0-10.857 2.346-20.221 7.045-28.068 3.693-6.303 8.606-11.275 14.755-14.925s12.793-5.51 19.948-5.629c3.915 0 9.049 1.211 15.429 3.591 6.362 2.388 10.447 3.599 12.238 3.599 1.339 0 5.877-1.416 13.57-4.239 7.275-2.618 13.415-3.702 18.445-3.275 13.63 1.1 23.87 6.473 30.68 16.153-12.19 7.386-18.22 17.731-18.1 31.002 0.11 10.337 3.86 18.939 11.23 25.769 3.34 3.17 7.07 5.62 11.22 7.36-0.9 2.61-1.85 5.11-2.86 7.51zm-31.26-123.01c0 8.1021-2.96 15.667-8.86 22.669-7.12 8.324-15.732 13.134-25.071 12.375-0.119-0.972-0.188-1.995-0.188-3.07 0-7.778 3.386-16.102 9.399-22.908 3.002-3.446 6.82-6.3113 11.45-8.597 4.62-2.2516 8.99-3.4968 13.1-3.71 0.12 1.0831 0.17 2.1663 0.17 3.2409z" />
      </Svg>
    </Button>
  )
}

const Windows = props => {
  return (
    <Button
      className={props.className}
      onClick={() => {
        window.location.assign(exeURL)
      }}
    >
      Windows
      <Svg
        className={props.className}
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 215.09 231.77"
        width="20px"
        height="20px"
      >
        <polygon points="98.7 110.68 215.09 109.82 215.09 0 98.7 17.86 98.7 110.68" />
        <polygon points="88.48 110.75 88.48 19.43 0 33.01 0 111.4 88.48 110.75" />
        <polygon points="88.48 121.33 0 120.83 0 199.09 88.48 212.53 88.48 121.33" />
        <polygon points="98.7 121.39 98.7 214.09 215.09 231.77 215.09 122.06 98.7 121.39" />
      </Svg>
    </Button>
  )
}

const Button = styled.div`
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-size: 14px;
  cursor: default;
  user-select: none;
  background: #fff;
  border: 1px solid #2a2a2a;
  color: #1a1a1a;
  cursor: pointer;
  &:hover {
    border-color: #03a9f4;
    color: #03a9f4;
  }
  &:hover svg {
    fill: #03a9f4;
  }
  &:active {
    opacity: 0.5;
  }
`
const Svg = styled.svg`
  margin-left: 9px;
  width: 20px;
  height: 20px;
  fill: #1a1a1a;
`



