import React from 'react'
import styled from 'styled-components'
import menuBar from '../../assets/menubar.png'

export const Cards = () => {
  return (
    <Container>

      <Card>
        <h2>Menu Bar App</h2>
        <Img src={menuBar} />
        <br />
        Transee lives in your menu bar
        and it starts automatically,
        so you can forget about it.
      </Card>

      <Card>
        <h2>Translation Bar</h2>
        Transee is always at your fingertips.<br />
        Press <Short>ctrl</Short><Short>alt</Short><Short>T</Short> or register
        your custom shortcut and start.
        Click anywhere on your desktop
        to hide the translation bar.
      </Card>

    </Container>
  )
}

const Container = styled.div`
  padding-top: 30px;
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items:center;
  background: #2a2a2a;
  width: 100%;
  min-height: 330px;
  font-weight: lighter;

  @media screen and (max-width: 1080px) {
    flex-direction: column;
  }
`

const Card = styled.div`
  box-sizing: border-box;
  text-align: center;
  font-size: 16px;
  background: none;
  border-radius: 5px;
  margin: 24px 0 0 0;
  max-width: 350px;
  height: 250px;
  padding: 12px;
  h2 {
    margin-bottom: 9px;
    color: #2182BD;
  }
`
const Note = styled.div`
  margin-top: 9px;
  font-size: 12px;
  color: #999
`
const Img = styled.img`
  margin: 18px 0;

  @media screen and (max-width: 300px) {
    width: 100%;
  }
`
const Short = styled.div`
  display: inline;
  padding: 1px 9px;
  border: 1px solid #fff;
  border-radius: 5px;
  font-size: 14px;
  margin: 1px;
`
const Row = styled.div`
  font-size: 14px;
  margin: 9px 3px;
`


