import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import { Downloads } from './components/downloads'
import { Header } from './components/header'
import { Footer } from './components/footer'
import { Cards } from './components/cards'
import logo from '../assets/icon_256x256.png'
import video from '../assets/rainbow.mp4'
import img from '../assets/rainbow.png'
import favicon from '../assets/favicon.png'
import './styles.css'

const TemplateWrapper = ({ children }) => (
  <div>
    <Helmet
      title="Transee"
      meta={[
        {
          name: 'description',
          content: 'Quick reference to Google translate desktop application',
        },
        {
          name: 'keywords',
          content:
            ' transee, google, translate, desktop, app, translation, electron, react, javascript, application, Alessio, Forte, software, developer, javascript, mac, windows, OS',
        },
      ]}
    >
      <link rel="shortcut icon" href={favicon} />
    </Helmet>

    <Page>
      <Header />
      <Section>

        <PageHeader>
          <Logo src={logo} alt="logo" />
          <Description>
            <h3>Transee</h3>
            <p>Simple and useful tool for quick translation</p>
            <Download>
              <Button href='#downloads'>Download</Button>
            </Download>
          </Description>
        </PageHeader>

        {/* <video id='video' src={video} autoPlay loop /> */}
        <Img src={img} />

      </Section>

      <Cards />
      <Downloads />

      <Footer />
    </Page>
  </div>
)

const Page = styled.div`
  position: absolute;
  top: 0;
  width: 100%;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
`
const Section = styled.div`
  display: flex;
  flex-direction:column;
  align-items:center;
  min-height: 914px;

  @media screen and (max-width: 600px) {
    min-height: auto;
  }
`
const PageHeader = styled.div`
  margin: 32px 0;
  display: flex;
  justify-content: space-between;
  width: 680px;

  @media screen and (max-width: 800px) {
    flex-direction: column;
    width: auto;
  }
`
const Logo = styled.img`
  margin: 0;
  height: 160px;
  object-fit: contain;
`
const Description = styled.div`
  text-align: right;
  color: #1a1a1a;
  padding: 3px;
  font-size: 20px;
  font-weight: 200;
  font-style: normal;
  & > h3 {
    font-size: 34px;
    margin-bottom: 9px;
    margin: 0
  }

  @media screen and (max-width: 800px) {
    text-align: center;
  }
`
const Img = styled.img`
  width: 100%;
  /* display: none;
  @media screen and (max-width: 800px) {
    display: block;
    width: 100%;
  } */
`
const Button = styled.a`
  text-decoration: none;
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-size: 14px;
  cursor: default;
  user-select: none;
  border: 1px solid #1a1a1a;
  background: #fff;
  color: #1a1a1a;
  cursor: pointer;
  &:hover {
    border-color: #03a9f4;
    color: #03a9f4;
  }
  &:active {
    opacity: 0.5;
  }
`
const Download = styled.div`
  box-sizing: border-box;
  margin-top: 24px;
  display: flex;
  flex-direction: row-reverse;

  @media screen and (max-width: 800px) {
    justify-content: center;
  }
`

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper



